$font-path: '../fonts/' !default;

// Units

$gridunit: 20px;
$gridunit-small: 10px;
$gridunit-large: 30px;
$uispeed: 250ms;
$uieasing: cubic-bezier(0.42, 0, 0.58, 1);
$base-radius: 8px;
$content-maxwidth: 1200px;

$grid-gap: 24px;
$column-width: 78px;

// Colors

$color01: #3a0e2f;
$color02: #EA4A72;
$color03: #ebebe8;
$color04: #F5F5F4;
$color05: #B88D97;
$color06: #D3D3D3;
$color07: #89697D;
$color08: #7F5A71;
$color09: #CAC0C4;
$color10: #6EAAD2;
$color11: #778B59;
$color12: #9B9B9B;
$color13: #D2D5DB; // iOS keyboard bg
$color14: #CBCBCB;
$color15: #F4DCE3; // brand02 @ .2 opacity
$color16: #633754;
$color17: #90C2E4;
$color18: #E6E0E2; // brand05 @ .2 opacity
$color19: #252222;
$color20: #3A0E2E;
$color21: #191c1e; // Solid color for opacity based gray backgrounds
$color22: #E4E4E2; // Darker light gray background

$color-white: #fff;
$color-black: $color01;
$color-primary: $color01;
$color-disabled: rgba(0, 0, 0, 0.5);
$color-text-dark: $color19;
$color-text-light: $color-white;

$color-gray-lightest: #fafafa;
$color-gray-lighter: #f5f5f5;
$color-gray-light: #eee;
$color-gray-medium: #aaa;
$color-gray-dark: #666;
$color-gray-darker: #444;
$color-gray-darkest: #222;

$color-primary: $color01;
$color-success: $color11;
$color-warning: $color09;
$color-danger: $color02;
$color-danger-light: lighten($color-danger, 40%);
$color-disabled: $color-gray-medium;
$color-active: $color02;
$color-pending: $color08;

// Sizes

$table-row-height: 42px;

$input-height: 34px;
$input-background: $color-gray-lightest;
$input-background-focus: $color-white;
$input-border-color: rgba(0, 0, 0, 0.1);
$input-border-color-hover: rgba(0, 0, 0, 0.15);
$input-border-color-focus: rgba(0, 0, 0, 0.30);

$button-default-height: $input-height;
$button-small-height: $gridunit;

$base-padding: 0 $gridunit $gridunit $gridunit;
$base-padding-large: 0 $gridunit-large $gridunit-large $gridunit-large;
$base-margin: $gridunit 0 0 0;
$base-margin-large: $gridunit-large 0 0 0;

$base-font-size: 15px;
$font-size-smallest: 10px;
$font-size-smaller: 12px;
$font-size-small: 14px;
$font-size-medium: 16px;
$font-size-large: 18px;
$font-size-larger: 24px;
$font-size-largest: 30px;

$base-line-height: 22px;
$line-height-small: 16px;
$line-height-medium: 26px;
$line-height-large: 30px;

$shadow01: 0 8px 32px 0 rgba($color21, 0.1);

$shadow02: 0 $gridunit / 2 $gridunit $gridunit / -2 $color-black;
$shadow03: 0 $gridunit / 2 $gridunit / 2 $gridunit / -2 rgba($color-black, 0.2);
$shadow-menus: 0 0 0 1px $color-gray-light, 0 $gridunit / 4 $gridunit / 2 $gridunit / -4 $color-black;

$font-family-text: 'ApercuPro', sans-serif;
$font-family-headings: 'ApercuPro', sans-serif;

$bold: 800;

$intents: (
	"primary": $color-primary,
	"success": $color-success,
	"warning": $color-warning,
	"danger": $color-danger,
	"active": $color-active,
	"neutral": $color-gray-medium,
	"pending": $color-pending
);