@import "../variables";
@import "../mixins";

.code-display {
    display: flex; flex-direction: column; justify-content: center; align-items: center; margin: $gridunit-large 0 0;
    @include mobile { margin: $base-margin; }
    &.loading {
        .loader { opacity: 1; pointer-events: all; }
    }
    .icon { width: 55px; height: 55px; background-size: contain; background-repeat: no-repeat; background-position: 50% 50%; }
    &-top { display: flex; flex-direction: column; justify-content: center; align-items: center; }
    &-middle {
        position: relative;
        @include not-mobile { margin: $gridunit 0 0; }
        H2, .loader { display: block; height: 86px; width: 180px; }
        .loader {
            position: absolute; width: 100%; height: 100%; z-index: 1; background: $color-white; opacity: 0; pointer-events: none; transition: opacity $uispeed $uieasing;
            &-element {
                $size: 32px;
                display: block; height: $size; width: $size;
                SPAN {
                    display: block; position: absolute; top: 0; left: 0; bottom: 0; right: 0; margin: auto; height: $size; width: $size;
                    &:before, &:after { content: ''; display: block; position: absolute; top: 0; left: 0; right: 0; bottom: 0; margin: auto; border-radius: 50%; }
                    &:before { height: $size; width: $size; border: 3px solid $color19; box-sizing: border-box; border-bottom: 3px solid transparent; animation: loader-anim1 1.5s cubic-bezier(0.67, 0, 0.375, 1) infinite; }
                    &:after { width: 6px; height: 6px; background: $color19; animation: loader-anim2 1.5s cubic-bezier(0.67, 0, 0.375, 1) infinite; }
                }
            }
        }
    }
    H1 { font-size: 20px; margin: $gridunit 0 0; }
    H2 {
        font-size: 86px; line-height: 1;
        @include mobile { font-size: 62px; font-weight: bold; }
    }
    H3 {
        font-size: $font-size-medium; color: $color05; font-weight: normal; line-height: $base-line-height; max-width: 400px;
        @include mobile { margin: 0; }
    }
    &.error {
        @include mobile { margin: 0; }
        .icon {
            width: 170px; height: 170px;
            @include mobile { width: 120px; height: 120px; }
        }
        H1 { font-size: $font-size-larger; font-weight: bold; }
        H2 { font-size: $font-size-large; line-height: $base-line-height; width: auto; height: auto; max-width: 380px; font-weight: normal; }
    }
}
@keyframes loader-anim1 {
    0% { transform: rotate(0deg); }
    40% { transform: rotate(180deg); }
    60% { transform: rotate(180deg); }
    100% { transform: rotate(360deg); }
}
@keyframes loader-anim2 {
    0% { transform: translate3d(0, -32px, 0) scale(0, 2); opacity: 0; }
    50% { transform: translate3d(0, 0, 0) scale(1.25, 1.25); opacity: 1; }
    100% { transform: translate3d(0, 8px, 0) scale(0, 0); opacity: 0; }
}