@import "../../scss/variables";
@import "../../scss/mixins";

.auth-methods {
    width: 100%; display: flex; flex: 0 0 auto; flex-wrap: wrap; justify-content: center; margin: $gridunit-large 0 $gridunit / 4 0;
    > LI { margin: 0; padding: $gridunit-large / 4 $gridunit-large / 4 0 $gridunit-large / 4;
        @include mobile { width: 100%; padding: $gridunit / 2 $gridunit / 4 0 $gridunit / 4; }
    }
    @include mobile { width: 100%; }
    &-method {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        align-items: center;
        border: none;
        background: $color-white;
        border-radius: 8px;
        width: 150px;
        height: 132px;
        cursor: pointer;
        background: $color-gray-lighter;
        .icon, SPAN { opacity: 0.5; transition: opacity $uispeed $uieasing; }
        .icon {
            width: 60px; height: 60px; background-size: contain; background-repeat: no-repeat; background-position: 50% 50%;
            @include mobile { width: 36px; height: 36px; }
        }
        &:hover {
            .icon, SPAN { opacity: 1; }
        }
        &:disabled { pointer-events: none; }
        SPAN {
            font-size: $font-size-smaller;
            font-weight: 600;
            letter-spacing: 1px;
            line-height: 1;
            margin-top: $gridunit-large / 2;
            @include mobile { margin: 0 0 0 $gridunit-small; font-size: $font-size-medium; }
        }
        @include mobile { width: 100%; height: $gridunit-large * 2; flex-direction: row; }
    }
}