@import "../variables";
@import "../mixins";

.buttons {
    padding: 0; margin: 10px -10px -10px -10px; list-style: none; display: flex; flex-direction: row; flex-wrap: wrap; align-items: center;
    @include mobile {
        min-width: 100%;
        > .buttons { min-width: 100%; max-width: 300px; }
    }
    &.layout-vertical-wide {
        margin: 20px -10px 0 -10px; display: block; width: 100%; max-width: 300px;
        > LI {
            margin: 0;
            &.primary + .primary { margin-top: 20px !important; }
            &.primary + .secondary { margin-top: 10px !important; }
            &.secondary + .secondary { margin-top: -9px !important; }
            .button { display: block; width: 100%; }
        }
    }
    > LI { margin: 10px; flex: 0 0 auto; }
    @include mobile {
        flex-direction: column; margin: $gridunit / 2 0 0 0;
        .button { margin: 0 !important; }
        > LI { margin: $gridunit / 2 0 0 0; }
    }
}