#root, .content-main { display: flex; width: 100%; }
.header-main {
    width: 100%;
    .verified-badge, .language-select { position: absolute; top: 0; }
    .verified-badge { left: 0; }
    .language-select { right: 0; }
}
.frame-main {
    width: 100%; height: 100%; display: flex; flex-direction: column; align-items: center; padding: $gridunit * 2 $gridunit * 2 $gridunit-large; box-sizing: border-box;
    .header-main { flex: 0 0 auto; }
    .content-main { flex: 1 0 auto; padding-top: $gridunit * 2; box-sizing: border-box; }
    @include mobile { padding: $gridunit; }
}
.content-centered {
    width: 100%; display: flex; flex-direction: column; align-items: center; box-sizing: border-box; justify-content: center; min-height: 100%;
    &-top, &-middle, &-bottom { position: relative; width: 100%; display: flex; flex-direction: column; justify-content: center; align-items: center; text-align: center; }
    &-top, &-bottom { flex: 0 0 auto; }
    @include mobile {
        > :first-child { flex: 1 0 auto; justify-content: flex-end; }
    }
    &-middle { flex: 0 0 auto; }
    &-bottom {
        flex: 1 0 auto; justify-content: flex-end;
        @include not-mobile { max-height: 160px; }
    }
}
