@import 'variables';
@import 'mixins';

@font-face {
	font-family: ApercuPro;
	src: url('#{$font-path}ApercuPro-Regular.ttf');
	font-weight: normal;
}

@font-face {
	font-family: ApercuPro;
	src: url('#{$font-path}ApercuPro-Bold.ttf');
	font-weight: bold;
}

BODY { font-size: $base-font-size; line-height: $base-line-height; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; font-family: $font-family-text; }

H1, H2, H3, H4, H5, H6 { line-height: 1.2; margin: 50px 0 0 0; font-weight: $bold; font-family: $font-family-headings; }
P { margin: $base-margin; }

H1 {
	font-size: 20px; font-weight: normal;
	@include mobile {
		font-size: 22px; margin: $base-margin;
	}
}
H2 {
	font-size: $font-size-medium; font-weight: normal; margin: $base-margin;
	@include mobile {
		font-size: $font-size-medium;
	}
}
