@import "../variables";
@import "../mixins";

.button {
    $hLarge: 56px;
    $hDefault: 46px;
    $hSmall: 36px;
    display: inline-block; border: none; cursor: pointer; text-decoration: none; text-align: center; margin: auto; background-color: transparent; min-width: 280px;
    white-space: nowrap; position: relative; transition: all $uispeed ease-in-out 0ms; box-sizing: border-box; user-select: none;
    &:hover { opacity: 0.8; }
    .text { transition: all $uispeed ease-in-out 0ms, }
    &.layout-default {
        background-color: $color02; color: $color-white; box-shadow: $shadow01; min-width: 220px;
        text-transform: uppercase; font-size: 14px; line-height: $hDefault; height: $hDefault; padding: 0 40px; font-weight: bold; letter-spacing: 1px; border-radius: 23px;
        @include not-desktop { padding: 0 20px; }
    }
    &.disabled { opacity: 0.5 !important; pointer-events: none; cursor: default; }
    &.layout-link { color: $color01; padding: 0; font-weight: bold; letter-spacing: 1px; text-transform: uppercase; font-size: 14px; line-height: $hDefault; height: $hDefault; }
}
