@import "../scss/variables";
@import "../scss/mixins";

BODY { display: flex; justify-content: center; align-items: center; }
#root { background: $color-white; width: 100%; height: 100%; min-height: 0; }
H1 { margin: 0; }
H1, H2 { max-width: 350px; }

.bg {
    position: absolute; top: 0; left: 0; right: 0; margin: 0 auto; width: 90%; height: 100%; max-width: 1260px; background-repeat: no-repeat; background-size: contain; background-position: 50% $gridunit-small; z-index: 0;
    @include mobile { top: 0; }
    & + * { z-index: 1; position: relative; }
}
.verified-badge {
    > DIV {
        width: 52px; height: 26px; background-size: cover; background-repeat: no-repeat; background-position: 50% 50%;
        @include mobile { width: 35px; height: 18px; }
    }
}
.logo-container { display: flex; justify-content: center; align-items: center; }
.logo {
    width: 160px; height: 35px; background-size: cover; background-repeat: no-repeat; background-position: 50% 50%;
    @include mobile { width: 120px; height: 27px; }
}